@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar-brand{
    font-family: 'Roboto';
    text-decoration: none;
    font-size: 28px;
    line-height: 64px;
    text-transform: uppercase;
    font-weight: bold;
}
.username{
    font-size: 15px;
    line-height: 20px;
    font-weight: bold;
    color: #000;
    margin: 0;
}
.user-email{
    font-size: 12px;
    line-height: 16px;
    color: #9a9a9a;
    margin: 0;
}
.navbar-user div{
    display: inline-block;
    margin: 0 5px;
    overflow: hidden;
}
.dropdown-toggle::after{
    display: inline;
    display: initial;
}
.fixed-top{
    background-color: #fff;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
}
.sidenav{
    border-right: 1px solid #f5f6f8;
    width: 274px;
    min-height: 100vh;
    float: left;
    clear: both;
    position: relative;
}
.sidenav ul{
    list-style: none;
    padding: 0;
}
.sidenav li {
    width: 271px;
    background-color: #fff;
    border-right: 3px solid transparent;
    background-repeat: no-repeat;
    padding: 20px 35px 20px 60px;
    background-position-y: center;
    background-position-x: 20px;
    cursor: pointer;
    color: #36445b;
    transition: 0.5s ease all;
    position: relative;
}
.sidenav li:hover {
    background-color: #3476ee;
    color: #fff;
}
.sidenav li .arrow{
    width: 19px;
    height: 6px;
    position: absolute;
    right: 15px;
    top: 50%;
    transform: translateY(-50%);
}
.sidenav-bottom-elements{
    position: absolute;
    bottom: 0;
}
.icon0:hover{
    background-image: url("https://raw.githubusercontent.com/shahaadesh5/Edkent-dashboard-react/main/dashboard-ui/public/images/icons/monitor-overlay.png") !important;
}
.icon1:hover{
    background-image: url("https://raw.githubusercontent.com/shahaadesh5/Edkent-dashboard-react/main/dashboard-ui/public/images/icons/icon2-overlay.png") !important;
}
.icon2:hover{
    background-image: url("https://raw.githubusercontent.com/shahaadesh5/Edkent-dashboard-react/main/dashboard-ui/public/images/icons/library.png") !important;
}
.icon3:hover{
    background-image: url("https://raw.githubusercontent.com/shahaadesh5/Edkent-dashboard-react/main/dashboard-ui/public/images/icons/icon4-overlay.png") !important;
}
.icon4:hover{
    background-image: url("https://raw.githubusercontent.com/shahaadesh5/Edkent-dashboard-react/main/dashboard-ui/public/images/icons/icon5-overlay.png") !important;
}
.icon5:hover{
    background-image: url("https://raw.githubusercontent.com/shahaadesh5/Edkent-dashboard-react/main/dashboard-ui/public/images/icons/icon6-overlay.png") !important;
}
.icon7:hover{
    background-image: url("https://raw.githubusercontent.com/shahaadesh5/Edkent-dashboard-react/main/dashboard-ui/public/images/icons/diamond-overlay.png") !important;
}
.sidenav-bottom-elements .settings{
    background-color: #e6f2ff;
    border-right: 3px solid #2569e6;
    background-repeat: no-repeat;
    padding: 40px 35px 40px 60px;
    background-position-y: center;
    background-position-x: 20px;
    cursor: pointer;
    color: #36445b;

}
.dashboard-wrapper{
    overflow: hidden;
    padding: 15px 15px 100px;
    background-color: #f5f6f8;
}
.upgrade-button{
    background-color: #4c89fa;
    width: 215px;
    border: none;
    outline: none;
    color: #fff;
    border-radius: 50px;
    height: 60px;
    font-size: 18px;
}
.dashboard-top-row{
    margin-top: 20px;
}
.top-row-grid{
    padding: 2px;
    background-color: #fff;
}
.stat-item-icon{
    border-top-right-radius: 45px;
    border-bottom-right-radius: 45px;
    background-color: #f5faff;
    padding: 20px;
    background-repeat: no-repeat;
    height: 100px;
    background-size: 50%;
    background-position: center;

}
.stat-number{
    font-size: 22px;
    line-height: 27px;
    font-weight: bold;
    margin: 0;
    color: #24282c;
}
.stat-item-text{
    padding: 26px 0;
}
.stat-text{
    color: #9d9fa3;
    font-size: 15px;
    line-height: 20px;
    margin: 0;
}
.graph-wrapper{
    overflow: hidden;
    background-color: #fff;
    margin-top: 20px;
    padding: 20px;
}
.modal-75w{
    width: 75%;
    max-width: none!important;
}
.input-group-append img {
    width: 100%;
}
.primary-color{
  color: #2569e6;
}
.page-adjust{
  margin-top: 90px;
}
.pull-right{
  float: right;
}
.container{
  clear: both;
  overflow: hidden;
}
